import React, { Component } from "react";
import PropTypes from "prop-types";
import { Header, Container, Button, Icon, Grid } from "semantic-ui-react";
import "../app/landpage.css";
import { init } from "ityped";
import { Link } from "react-router-dom";

const HomepageHeading = ({ mobile }) => (
  <Container text>
    <Grid>
      <Grid.Row>
        <Header
          as="h1"
          inverted
          style={{
            fontSize: mobile ? "2em" : "4em",
            fontWeight: "normal",
            marginBottom: 0,
            marginTop: mobile ? "1.5em" : "3em",
            color: "black"
          }}
          id="myElement"
        />
      </Grid.Row>

      <Grid.Row></Grid.Row>

      <Grid.Row></Grid.Row>
    </Grid>
    <Button primary size="huge" as={Link} to="/home">
      Get Started
      <Icon name="right arrow" />
    </Button>
  </Container>
);

HomepageHeading.propTypes = {
  mobile: PropTypes.bool
};

export default class LandPage extends Component {
  componentDidMount() {
    const myElement = document.querySelector("#myElement");
    init(myElement, {
      cursorChar: "_",
      showCursor: false,
      strings: ["Hell o! ", "I'm Dex"]
    });
  }
  render() {
    return (
      <div>
        <HomepageHeading />
      </div>
    );
  }
}
